import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";
export default {

    layout: 'Adm-content',
    methods: {
        download() {
            window.html2canvas = html2canvas;
            var doc = new jsPDF('p', 'pt', 'a4');
            doc.html(document.querySelector("#html"), {
                callback: function (pdf) {
                    pdf.save("print.pdf");
                }
            })

        }
    }

}
